@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/rubik-400.woff2") format("woff2"), url("../fonts/rubik-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Rubik";
  font-display: swap;
  src: url("../fonts/rubik-700.woff2") format("woff2"), url("../fonts/rubik-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  font-family: "Rubik", "Arial", sans-serif;
  color: #111111;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #111111;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #111111;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1450px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  padding: 23px 0;
  background-color: #111111;
  z-index: 1;
}
.header .container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 76px;
}
.header__logo {
  color: #ffffff;
}

.header__logo {
  display: flex;
  align-items: center;
  margin-right: 45px;
}

.header__cart {
  position: relative;
  transition: all 0.3s ease-in;
}
.header__cart-number {
  position: absolute;
  bottom: 0;
  right: -6px;
  width: 16px;
  height: 16px;
  background-color: #FCB500;
  font-size: 12px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__cart:hover, .header__cart:focus {
  opacity: 0.7;
}

@media (max-width: 1409px) {
  .header__logo {
    margin-right: 0;
  }
  .header .container {
    gap: 10px;
    justify-content: space-between;
  }
}
@media (max-width: 1279px) {
  .main-nav__item {
    font-size: 16px;
  }
}
.main-nav {
  margin-right: 30px;
}
.main-nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  gap: 50px;
}
.main-nav__link {
  color: #ffffff;
  letter-spacing: 0.02em;
  white-space: nowrap;
}
.main-nav__link.is-active {
  opacity: 0.6;
}
.main-nav__toggle {
  display: none;
}
.main-nav__item {
  transition: all 0.3s ease-in;
}
.main-nav__item:hover, .main-nav__item:focus {
  opacity: 0.7;
}

@media (max-width: 1279px) {
  .main-nav__list {
    gap: 20px;
  }
}
@media (max-width: 1409px) {
  .main-nav {
    margin-right: 0;
  }
  .main-nav__list {
    gap: 20px;
  }
}
@media (max-width: 1023px) {
  .main-nav__list {
    position: absolute;
    top: 84px;
    left: 0;
    background: #111111;
    align-items: center;
    flex-direction: column;
    width: 100%;
    padding-bottom: 15px;
    transition: all 0.4s ease-in;
    opacity: 1;
  }
  .main-nav__list.is-hidden-mobile {
    left: -100%;
    opacity: 0;
  }
}
.intro {
