  padding: 0 40px;
}
.intro .container::before {
  position: absolute;
  top: -92px;
  right: -63px;
  content: "";
  width: 877px;
  height: 1051px;
  background-image: url("../img/content/lemon.png");
  background-size: cover;
  z-index: -1;
}

.intro__title {
  margin: 0;
  font-size: 144px;
  line-height: 150px;
  line-height: 160px;
