}

.intro__subtext {
  margin: 0;
  max-width: 360px;
  letter-spacing: 0.03em;
  line-height: 21px;
}

@media (max-width: 1439px) {
  .intro__title {
    font-size: 100px;
    line-height: 105px;
  }
  .intro .container::before {
    transform: scale(0.9);
    top: -143px;
  }
}
@media (max-width: 1279px) {
  .intro__title {
    font-size: 85px;
    line-height: 90px;
  }
  .intro__text {
    max-width: 500px;
  }
  .intro__subtitle {
    max-width: 350px;
  }
  .intro__subtext {
    max-width: 330px;
  }
}
@media (max-width: 1023px) {
  .intro__title {
    font-size: 55px;
    line-height: 60px;
    max-width: 420px;
  }
  .intro .container::before {
