    right: -120px;
  }
}
@media (max-width: 767px) {
  .intro {
    padding: 32px 0;
  }
  .intro .container::before {
    transform: scale(0.4);
    right: -255px;
    top: -246px;
  }
  .intro__text {
    margin-bottom: 50px;
    font-size: 18px;
    line-height: 21px;
