  }
  .intro__title {
    font-size: 40px;
    line-height: 45px;
  }
}
@media (max-width: 500px) {
  .intro {
    padding-bottom: 100px;
  }
  .intro .container::before {
    top: -112px;
    right: -309px;
  }
  .intro__subtext {
    max-width: 80%;
  }
}
.order {
  background-color: #111111;
  padding: 120px 0 112px 0;
}
.order .container {
  max-width: 1296px;
}

.order__title {
  margin: 0;
  margin-bottom: 92px;
  text-align: center;
  font-size: 48px;
  line-height: 57px;
  text-transform: uppercase;
  color: #ffffff;
  letter-spacing: 0.03em;
}
.order__title-highlight {
  color: #FCB500;
  display: block;
}

.order__price {
  margin: 0;
  display: flex;
  justify-content: center;
  gap: 48px;
  align-items: center;
  margin-bottom: 59px;
  padding-left: 45px;
}
.order__price-old {
  font-size: 36px;
  line-height: 42px;
  color: #ADADAD;
  position: relative;
}
.order__price-old::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  height: 1px;
  width: 120%;
  transform: translate(-50%, -50%);
  background-color: #ADADAD;
}
.order__price-new {
  font-size: 48px;
  line-height: 57px;
  color: #ffffff;
}

.order__items {
  display: flex;
  justify-content: center;
  gap: 140px;
  margin-bottom: 64px;
}

.order__item img {
  margin-bottom: 86px;
}
.order__item--big img {
  margin-bottom: 57px;
}
.order__item--big .order__subtitle {
  font-size: 36px;
  line-height: 46px;
}
.order__item--big .order__subtitle-highlight {
  font-size: 24px;
}

.order__subtitle {
  margin: 0;
  color: #ffffff;
  font-weight: 400;
  text-align: center;
  font-size: 18px;
  letter-spacing: 0.03em;
}
.order__subtitle-highlight {
  display: block;
  color: #FCB500;
}

.order__button.btn {
  display: block;
  margin: 0 auto;
  font-size: 18px;
  border-radius: 7px;
  min-width: 276px;
  padding: 21px 32px;
}

@media (max-width: 1279px) {
  .order__items {
    gap: 10px;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .order__title {
    font-size: 40px;
    line-height: 45px;
    margin-bottom: 36px;
  }
  .order__items {
    flex-direction: column;
    gap: 50px;
  }
  .order__item img {
    margin: 0 auto;
    margin-bottom: 30px;
  }
}
.hamburger {
  display: none;
  margin: 0 auto;
  width: 35px;
  height: 25px;
  position: relative;
  background: none;
  border: none;
  position: absolute;
  top: 28px;
  left: 50%;
  transform: translateX(-50%);
}
@media (max-width: 1023px) {
  .hamburger {
    display: block;
  }
}

.hamburger .bar {
  padding: 0;
  width: 35px;
  height: 4px;
  background-color: #ffffff;
  display: block;
  border-radius: 4px;
  transition: all 0.4s ease-in-out;
  position: absolute;
}

.bar1 {
  top: 0;
}

.bar2,
.bar3 {
  top: 10.5px;
}

.bar4 {
  bottom: 0;
}

.hamburger .bar1 {
  transform-origin: 5%;
}

.hamburger .bar4 {
  transform-origin: 5%;
}

.hamburger.is-closed > .bar1 {
  transform: rotate(45deg);
  height: 3px;
  width: 35px;
}

.hamburger.is-closed > .bar3 {
  transform: rotate(45deg);
  height: 3px;
  background-color: transparent;
}

.hamburger.is-closed > .bar2 {
  transform: rotate(-45deg);
  height: 3px;
  background-color: transparent;
}

.hamburger.is-closed > .bar4 {
  transform: rotate(-45deg);
  height: 3px;
  width: 35px;
}

.footer {
  padding: 40px 0;
  background-color: #2c39f2;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(17, 17, 17, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #111111;
  background-color: #FCB500;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

