
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Rubik";

  font-display: swap;
  src:
    url("../fonts/rubik-400.woff2") format("woff2"),
    url("../fonts/rubik-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Rubik";

  font-display: swap;
  src:
    url("../fonts/rubik-700.woff2") format("woff2"),
    url("../fonts/rubik-700.woff") format("woff");
}
